.trailsText {
    position: relative;
    width: 100%;
    height: 80px;
    line-height: 80px;
    color: #FFFFFF;
    font-size: 6em;
    font-weight: 800;
    letter-spacing: -0.05em;
    will-change: transform, opacity;
    overflow: hidden;
  }
  
  .trailsText > div {
    padding-right: 0.05em;
    overflow: hidden;
  }
  
  .container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh; /* Full viewport height */
    background-color: #282c34; /* Dark background color */
  }
  